const tcvn3Map = tcvn3Pos.reduce((map, item, index) => {
  map[item] = unicodePos[index];
  return map;
}, {});

const tcvn3ToUnicode = (text, convert = true) => {
  if (!convert) {
    return text || '';
  }
  return (text || '')
    .split('')
    .map(char => {
      const code = char.charCodeAt(0);
      const unicode = tcvn3Map[code];
      if (unicode) {
        return String.fromCharCode(unicode);
      }
      return char;
    }, [])
    .join('');
};

module.exports = {
  tcvn3ToUnicode,
};
